<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <div class="item">
                <label>
                    订单状态
                </label>
                <span>
                    <p style="color:green;">{{orderStatusTxt}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    收货人
                </label>
                <span>
                    <p class="flex-b">
                        <span>{{consignee}}</span>
                        <span style="text-align: right;">{{phone}}</span>
                    </p>
                </span>
            </div>
            <div class="item">
                <label>
                    收货地址
                </label>
                <span>
                    <p style="height:auto;">{{address}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    客户备注
                </label>
                <span>
                    <p v-if="memo!=''">{{memo}}</p>
                    <p v-if="memo==''">暂无备注</p>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="order-content" v-for="(item,i) in orderItems" :key="i">
                <img :src="item.thumbnail" alt="">
                <div class="text-right">
                    <div class="title-head">
                        <div>{{item.name}}</div>
                        <span style="color:red;">￥{{item.price}}</span>
                    </div>
                    <div class="title-info">
                        <div class="infor">{{item.sku}}</div>
                        <div class="numbers"><i style="color:red;">x{{item.quantity}}</i></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    商品总价
                </label>
                <span>
                    <p><i style="color:red;">￥{{amount}}</i></p>
                </span>
            </div>
            <div class="item">
                <label>
                    优惠金额
                </label>
                <span>
                    <p><i style="color:red;">￥{{discountAmount}}</i></p>
                </span>
            </div>
            <div class="item">
                <label>
                    运费
                </label>
                <span>
                    <p><i style="color:red;">￥{{freight}}</i></p>
                </span>
            </div>
            <div class="item">
                <label>
                    实付款
                </label>
                <span>
                    <p><i style="color:red;">￥{{payMoney}}</i></p>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    订单编号
                </label>
                <span>
                    <p>{{orderSn}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    创建时间
                </label>
                <span>
                    <p>{{orderTime}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    付款时间
                </label>
                <span>
                    <p v-if="payDate!=''">{{payDate}}</p>
                    <p v-if="payDate==''">暂无创建时间</p>
                </span>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    export default{
        created(){
            let that=this;
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.$route.query.id!=undefined){
                this.id=this.$route.query.id;
            }
            that.MintUi.Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
            Exotic.ajaxData(that.token,Exotic.urlMap.other.order, {orderId: that.id}, 'get',function(data){
                console.log(data)
                try {
                    that.orderStatusTxt=data.orderStatusTxt;//订单状态
                    that.consignee=data.consignee; //收货人
                    that.address=data.address; //地址
                    that.memo=data.memo;//备注

                    for(let i in data.orderItems){
                        data.orderItems[i]['thumbnail']=Exotic.imageUrl+data.orderItems[i]['thumbnail'];
                    }
                    //商品图片
                    that.orderItems=data.orderItems;

                    that.amount=data.amount; //商品总价
                    that.discountAmount=data.discountAmount;//优惠金额
                    that.freight=data.freight;//运费
                    that.payMoney=data.payMoney;//实付款
                    that.phone=data.phone;

                    //订单编号
                    that.orderSn=data.orderSn;
                    //创建时间
                    var createDate = new Date(data.createDate).Format("yyyy-MM-dd hh:mm:ss");
                    that.orderTime=createDate;
                var payDate;
                    if(data.payDate==null||data.payDate==""){
                        payDate="";
                    }else{
                        payDate = new Date(data.payDate).Format("yyyy-MM-dd hh:mm:ss");
                    }
                    that.payDate=payDate;
                    that.MintUi.Indicator.close()
                } catch (error) {
                    
                }
            })
        },
        data(){
            return {
                token:null,
                id:'',//ID
                orderSn:'',//订单编号
                orderTime:null,//时间
                orderItems:'',
                discountAmount:'',//优惠金额
                consignee:'',//用户名称
                address:'',//地址
                freight:'',//运费
                payMoney:'',//实付款
                fansName:'',//联系人
                phone:'',//联系电话
                amount:'',//总价
                payDate:'',//支付时间
                trackingNo:'',//快递单号
                memo:'',//备注
                orderStatusTxt:''//状态
            }
        },
        methods:{
            
        },
        mounted(){
          
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
</style>




